import http from "../http-common";
import SockJS from "sockjs-client";
import Stomp from "webstomp-client";
import store from "../store.js";
import router from "../router";
import config from "../myconfig.js";

class QRLoginService {

  qrLogin(sessionid) {
    return http.get("../qr-code/login/" + sessionid);
  }

  getCurrentSessionID() {
    return http.get("../qr-code/sessionid");
  }

  connect(sessionid) {

    this.socket = new SockJS(config.websocketURL);
    this.stompClient = Stomp.over(this.socket);
    this.stompClient.connect(
      {},
      frame => {

        this.connected = true;
        console.log(frame);

        this.stompClient.subscribe("/topic/messages/" + sessionid, msg => {
          console.log(msg.body);

          if (msg.body.startsWith("Login Success")) {
            let userName = msg.body.split(":")[1];
            this.loginSuccess(userName);
          }
        });

        // send a hello message
        this.stompClient.send("/app/hello/" + sessionid, JSON.stringify("hello from QR client!"), {});

      },
      error => {
        console.log(error);
        this.connected = false;
      }
    );
  }

  disconnect() {
    if (this.stompClient) {
      this.stompClient.disconnect();
    }
    this.connected = false;
  }

  loginSuccess(userName) {

    // check login status
    http.get("../qr-code/loginStatus").then(resp => {
      if (resp.status === 200) {
        // get user roles
        http.get("/userinfo")
          .then((resp) => {

            console.log(resp.data);
            localStorage.setItem("LoggedUserRoles", resp.data.split("|")[1]);
            store.commit("loggedIn", resp.data);

            localStorage.setItem("LoggedUser", userName);

            //window.location.href = "/dashboard";
            router.replace("/dashboard");
            
          })
          .catch((error) => console.log(error));
      }
    });
  }
}

export default new QRLoginService();
