<template>
  <div id="login" class="loginWindowDiv">
    <h3 class="text-center pt-5">Welcome to My Tutorials</h3>
    <div class="container">
      <div id="login-row" class="row justify-content-center align-items-center">
        <div id="login-column" class="col-md-6">
          <div id="login-box" class="col-md-12">
            <form id="login-form" class="form" @submit.prevent="login()">
              <b-row><b-col offset="4" class="text-center"><h3>Login</h3></b-col>
              <b-col class="text-right">
                <b-link @click="toggleLogin()">{{loginLinkText}}</b-link>
              </b-col>
              </b-row>
              <b-alert :show="errorMessage.length" variant="warning">
                {{ errorMessage }}
              </b-alert>
              <div id="login-qrcode" v-show="showQRcode">
                <br/>
                <!--<b-img center src="qr-code.png" /> -->
                <b-img center :src="qrCodeURL" />
              </div>
              <div id="login-content" v-show="!showQRcode"> 
                <div class="form-group">
                  <label for="username" class="text-info">Username:</label
                  ><br />
                  <input
                    type="text"
                    name="username"
                    id="username"
                    v-model="username"
                    class="form-control"
                    autofocus
                  />
                </div>
                <div class="form-group">
                  <label for="password" class="text-info">Password:</label
                  ><br />
                  <input
                    type="password"
                    name="password"
                    id="password"
                    v-model="password"
                    class="form-control"
                  />
                </div>
                <div class="form-group">
                  <label for="remember-me" class="text-info"
                    ><span>Remember me </span>
                    <span
                      ><input
                        id="rememberMe"
                        name="rememberMe"
                        v-model="rememberMe"
                        type="checkbox" /></span></label
                  ><br />
                  <input
                    type="submit"
                    name="submit"
                    class="btn btn-info btn-md"
                  />
                </div>
                <div id="register-link" class="text-right">
                  <b-link href="#" class="text-info">Register here</b-link>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../http-common";
import Vue from "vue";
import qrLoginService from '../services/QRLoginService';
import config from "../myconfig.js";

export default {
  name: "Login",
  data() {
    return {
      errorMessage: "",
      messageSessionExpired: "Your session has expired. Please log in",
      username: "",
      password: "",
      rememberMe: false,
      loginLinkText: "QRCode Login",
      showQRcode: false,
      currentSessionID: "",
      qrCodeURL: ""
    };
  },
  methods: {
    login() {
      let self = this;

      if (self.username == "") {
        this.errorMessage = "username field is empty";
        //this.$bvToast.show('err-toast');
        //util.showFailureToast(this, "username field is empty");
        return;
      }

      if (self.password == "") {
        this.errorMessage = "password field is empty";
        //this.$bvToast.show('err-toast');
        //util.showFailureToast(this, "password field is empty");
        return;
      }

      this.username = self.username;

      let bodyFormData = new FormData();
      bodyFormData.set("username", self.username);
      bodyFormData.set("password", self.password);
      bodyFormData.set("remember-me", self.rememberMe);

      axios
        .post("../login", bodyFormData, {
          headers: { "Content-Type": "application/x-www-form-urlencoded" },
        })
        .then((resp) => this.loginSuccessful(resp))
        .catch((error) => this.loginError(error));
    },

    loginSuccessful(resp) {
      if (resp.status === 200) {
        console.log(resp.request.responseURL);
        if (resp.request.responseURL.indexOf("?") === -1) {
          console.log("Auth successful");
          localStorage.setItem("LoggedUser", this.username);
          this.$router.replace("/dashboard");

          // get user roles
          axios
            .get("/userinfo")
            .then((resp) => {
              console.log(resp.data);
              localStorage.setItem("LoggedUserRoles", resp.data.split("|")[1]);
              this.$store.commit("loggedIn", resp.data);
            })
            .catch((error) => console.log(error));
        } else {
          let params = resp.request.responseURL.split("?");
          if (params[1] === "error") {
            this.errorMessage = "Invalid username and password";
            //this.$bvToast.show('err-toast');
            //util.showFailureToast(this, 'Invalid username and password');
            Vue.showErrorToast({ title: this.errorMessage });
            localStorage.clear();
          }
          return;
        }
      } else {
        console.log(resp);
        return;
      }
    },

    loginError(error) {
      console.log("*********** ERROR **********");
      console.log(error);
      this.errorMessage =
        "Failed on " + axios.defaults.baseURL + ": " + error.toString();
    },

    toggleLogin() {
      this.showQRcode = !this.showQRcode;
      this.loginLinkText = this.showQRcode ? "Form Login" : "QRCode Login";

      // somehow this.$cookies is not working, always return null
      //console.log("current session id: " + this.$cookies.get("JSESSIONID"));

      if (this.showQRcode) {
        this.qrCodeURL = config.baseQRURL;

        qrLoginService.getCurrentSessionID().then(resp => {
          this.currentSessionID = resp.data;
          console.log("current session id: " + this.currentSessionID);
  
          qrLoginService.connect(this.currentSessionID);
        });
      } else {
        qrLoginService.disconnect();
      }
    }
  },
};
</script>

<style>
.loginWindowDiv {
  background-color: #e6f3f7;
  width: 100%;
  height: auto;
  bottom: 0px;
  top: 0px;
  left: 0;
  position: absolute;
}
#login .container #login-row #login-column #login-box {
  margin-top: 120px;
  max-width: 600px;
  height: 400px;
  border: 1px solid #9c9c9c;
  background-color: #eaeaea;
}
#login .container #login-row #login-column #login-box #login-form {
  padding: 20px;
}
#login
  .container
  #login-row
  #login-column
  #login-box
  #login-form
  #register-link {
  margin-top: -85px;
}
</style>
